import React from 'react'
import quizTypes from '../assets/jsons/quizTypes.json'
import { useNavigate } from 'react-router-dom'
import { Container, Box, Grid, Card, CardContent, CardMedia } from '@mui/material'

export default function Home() {
  const navigate = useNavigate();

  const handleClick = (e) => {
    navigate('/questions', { state: { quizType: e.name } })
  }

  return (
    <Container maxWidth="xl">
      <div className="title">
        <h1 className='title-text'>Loksewa Quiz</h1>
      </div>
      <Box sx={{ flexGrow: 1 }}>
        <Grid container direction="row" rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }} className="grid">
          {quizTypes.map((e, index) => {
            return (
              <Grid item xs={8} key={index}>
                <Card sx={{ maxWidth: 345 }} onClick={() => handleClick(e)} className="card">
                  <CardMedia
                    component="img"
                    height="140"
                    image={e.image}
                    alt={e.name}
                  />
                  <CardContent>
                    <h2>{e.name}</h2>
                    <p>{e.description}</p>
                  </CardContent>
                </Card>
              </Grid>)
          })}
        </Grid>
      </Box>
    </Container>
  )
}
